.banner {
  background: url("https://media.cntraveler.com/photos/5db1d0dd11c1e500092e7133/master/pass/airbnb-ski-aspen.jpg")
    center center;
  height: 57vh;
  position: relative;
}

.banner_info {
  background-color: black;
  color: white;
  padding-top: 27px;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 40px;
  margin-bottom: 40px;
  width: 300px;
}

.banner_info > button {
  background-color: #ff7779;
  color: white;
  text-transform: inherit;
  margin-top: 20px;
  font-weight: 600;
}

.banner_info > button:hover {
  background-color: white;
  color: #ff7779;
}

.banner_info > h5 {
  margin-top: 10px;
}

.banner_search {
  display: flex;
  flex-direction: column;
}

.banner_searchButton {
  background-color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  color: #ff7779 !important;
}

.close_ads {
  position: absolute;
  cursor: pointer;
  left: 275px;
  top: 50px;
}

.close_ads:hover {
  color: red;
}
